class IconButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = { colour: props.colour };
  }

  componentDidMount() {
    const { enabled } = this.props;
    const type = typeof enabled;
    if (!enabled) {
      this.setState({
        colour: colours.grey,
      });
    } else if (type === 'string') {
      this.setState({
        colour: enabled,
      });
    } else if (enabled || type === 'undefined') {
      this.setState({
        colour: colours.blue,
      });
    }
  }

  componentWillReceiveProps({ enabled }) {
    const type = typeof enabled;
    if (enabled === false) {
      this.setState({
        colour: colours.grey,
      });
    } else if (type === 'string') {
      this.setState({
        colour: enabled,
      });
    } else if (enabled || type === 'undefined') {
      this.setState({
        colour: colours.blue,
      });
    }
  }

  render() {
    const { onClick, icon, text, className, Button, classes } = this.props;
    const { colour } = this.state;

    const classNames = [`iconButton-${colour}`, 'iconButton', classes.button];
    if (className) {
      classNames.push(className);
    }
    return (
      <button onClick={onClick} className={classNames.join(' ')}>
        {icons[icon] && icons[icon](colour)}
        {Button || <div style={{ color: colour }} className="iconButtonText">{text}</div>}
      </button>
    );
  }
}

IconButton.defaultProps = {
  onClick: () => {},
  classes: {},
  className: {},
  enabled: true,
  colour: colours.blue,
  Button: null,
};
IconButton.propTypes = {
  onClick: PropTypes.func,
  classes: PropTypes.object,
  className: PropTypes.object,
  enabled: PropTypes.any,
  colour: PropTypes.string,
  Button: PropTypes.element,
  icon: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
};

export default withStyles(styles)(IconButton);
